body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

@media (max-width: 600px) {
  .facet_sidebar {
    display: none;
  }
}



/* Styling for the About Me page */
.about{
  background-color: black;
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.mypic{
  display: flex;
  height:  600px;
  width: 100%;

}

.about h1{
  color:antiquewhite;
}

.about p{
  color: antiquewhite;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 25px;
}

/* Styling for Nav-Bar */
.home{
  background-color: cadetblue;
}
h1{
color: rgb(31, 25, 22);
}

header{
  display: flex;
  justify-content: space-between;
}

.flex-row{
display: flex;
justify-content: right;
padding: 10px;
}



.nav-tabs{
  display: flex;
  justify-content: space-evenly;
  background-color: #232222;
  color: whitesmoke;
}

.nav-item{
  background-color: rgb(5, 5, 5);
}

.nav-link{
  color: rgb(200, 202, 202);
}

li.nav-item{
  color: antiquewhite;
}

/*  Styling for the Portfolio Page */
.port-page{
  display: flex;
  background-color: black;
}

.portfolio h1{
  color:antiquewhite;
}

.port-page p{
  display: flex;
  align-items: center;
  color: antiquewhite;
}

.port-page footer{
  color: aqua;
}


/*  Styling for the Resume Page */
.resume{
  background-color: black;
  color: antiquewhite;
}

.resume h1{
  color: antiquewhite;
}

/*  Styling for the Contact Page */
.contact{
background-color: black;
}

.contact h1{
  color: antiquewhite;
}

.contact p{
  color: antiquewhite;
}

.github-image .logo{
  display: flex;
  height: 65px;
  background: antiquewhite;
}

.port-page a{
  color:antiquewhite;
}

.social-container{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  
  
}

.social-container a{
display: flex;
justify-content: center;
background-color: #232222;
color: rgb(95, 110, 116);


}

.teams{
  display: flex;
  justify-content: center;
  gap: 5px;
}

.about .mets{
  color: rgb(24, 24, 224);
}

.about .isles{
  color: rgb(250, 133, 0);
}